import React, { Component } from 'react';
import axios from 'axios';
import reactReferer from 'react-referer';
import { Row, Col } from 'antd';
import { Form, Input, Button, Select, Radio, DatePicker } from 'antd';
import { Modal} from 'antd';
import './App.css';
const FormItem = Form.Item;
const Option = Select.Option;
const RadioGroup = Radio.Group;

const eng = {
  type_number: 'Please input a valid number',
  required: 'Please fill out this field',
  confirmation: 'Thank you for your request! We will contact you soon',
  bt: 'Boat',
  pd: 'Personal details',
  mft: 'Manufacturer',
  md: 'Model',
  yr: 'Year built',
  vt: 'Value of trailer',
  vv: 'Value of vessel',
  is: 'Insurance sum',
  tv: 'Type of vessel',
  mb: 'Motorboot',
  sb: 'Sailing boat',
  tep: 'Total engine power',
  vom: 'Value of outboard motor',
  ca: 'Cruising Area',
  ec: 'Europe coast',
  ei: 'Europe Inside',
  sal: 'Salutation',
  mr: 'Mr.',
  mrs: 'Mrs.',
  ln: 'Last name',
  fn: 'First name',
  db: 'Date of birth',
  tel:  'Phone number',
  zc: 'Zip code',
  str: 'Street',
  cty: 'City',
  mail: 'Email',
}

const ger = {
  type_number: 'Bitte geben Sie eine gltige Nummer ein',
  required: 'Bitte fllen Sie dieses Feld aus',
  confirmation: 'Vielen Dank fr Ihre Anfrage! Wir setzen uns kurzfristig mit Ihnen in Verbindung',
  bt: 'Boot',
  pd: 'Ihre Kontaktdaten',
  mft: 'Hersteller ',
  md: 'Modell ',
  yr: 'Baujahr ',
  vt: 'Wert Trailer',
  vv: 'Kaufpreis ',
  is: 'Versicherungssumme',
  tv: 'Schiffstyp ',
  mb: 'Motorboot',
  sb: 'Segelboat',
  tep: 'Gesamtmotorleistung',
  vom: 'Wert Auenborder',
  ca: 'Fahrtgebiet',
  ec: 'Europa kste',
  ei: 'Europa binnen',
  sal: 'Anrede',
  mr: 'Herr',
  mrs: 'Frau',
  ln: 'Nachname',
  fn: 'Vorname',
  db: 'Geburtsdatum',
  tel:  'Telefon',
  zc: 'Postleitzahl',
  str: 'Strae',
  cty: 'Stadt',
  mail: 'E-Mail',
}

class PantForm extends Component {
  constructor(props) {
    super(props);
    console.log(props.match.params.lang)
    if(props.match.params.lang === 'eng' || props.match.params.lang === undefined)
    {
      this.state = {
        default: 'eng',
        content: eng,
        referer: reactReferer.referer(),
      };
    }

    if(props.match.params.lang === 'ger'){
      this.state = {
        default: 'ger',
        content: ger,
        referer: reactReferer.referer(),
      };
    }
  }

  handleLanguage = (e) => {
    if(e==='eng'){
      this.setState({ content: eng});
    }else{
      this.setState({ content: ger});
    }
  }

  handleSubmit = (e) => {
    var confirmation = this.state.content.confirmation
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {

        axios.post('https://formspree.io/multiteamhh@pantaenius.com', values)
        .then(function (response) {
          console.log(response);
          Modal.success({
            title: confirmation,
            content: ' ',
          });
        })
        .catch(function (error) {
          console.log(error);
        });

        console.log('Received values of form: ', values);
      }
    });
  }

  componentDidMount(){
    this.props.form.setFieldsValue({
       url: this.state.referer,
     });
  }

  render() {

    const { getFieldDecorator } = this.props.form;
    //const { autoCompleteResult } = this.state;

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 10 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
    };

    const colLayout = {
      xs: 24, sm: 12, md: 12,
    }

    const currency = (
      <Select defaultValue="USD" style={{ width: 80 }}>
        <Option value="USD">USD</Option>
        <Option value="EUR">EUR</Option>
      </Select>
    )

    const powerUnity = (
      <Select defaultValue="PS" style={{ width: 80 }}>
        <Option value='PS'>PS</Option>
        <Option value='KW'>KW</Option>
      </Select>
    )


    var content = this.state.content;

    return (

      <Form onSubmit={this.handleSubmit}>

        <Row gutter={8}>
        <Col {...colLayout}>
          <FormItem
            {...formItemLayout}
            label='Language'
          >

              <Select onChange={this.handleLanguage} defaultValue={this.state.default}>
                <Option value='eng'>English</Option>
                <Option value='ger'>German</Option>
              </Select>

          </FormItem>
        </Col>
      </Row>

      <Row>
        <Col offset={2}>
          <h1>{content.bt}</h1>
        </Col>
      </Row>

        <Row gutter={8}>
          <Col {...colLayout}>

            <FormItem>
              {getFieldDecorator('url',
            )(
              <Input type="hidden" />
            )}

            </FormItem>

            <FormItem
              {...formItemLayout}
              label={content.mft}
            >
              {getFieldDecorator('Manufacturer', {
                rules: [{
                  type: 'string', message: '',
                }, {
                  required: true, message: content.required,
                }],
              })(
                <Input />
              )}
            </FormItem>
          </Col>
          <Col {...colLayout}>
            <FormItem
              {...formItemLayout}
              label={content.md}
            >
              {getFieldDecorator('Model', {
                rules: [{
                  type: 'string', message: '',
                }, {
                  required: true, message: content.required,
                }],
              })(
                <Input />
              )}
            </FormItem>
          </Col>

          <Col {...colLayout}>
            <FormItem
              {...formItemLayout}
              label={content.yr}
            >
              {getFieldDecorator('Year built', {
                rules: [{
                  required: true, message: content.required,
                }],
              })(
                <Select >
                  <Option value="1990">1990</Option>
                  <Option value="1991">1991</Option>
                  <Option value="1992">1992</Option>
                  <Option value="1993">1993</Option>
                  <Option value="1994">1994</Option>
                  <Option value="1995">1995</Option>
                  <Option value="1996">1996</Option>
                  <Option value="1997">1997</Option>
                  <Option value="1998">1998</Option>
                  <Option value="1999">1999</Option>
                  <Option value="2000">2000</Option>
                  <Option value="2001">2001</Option>
                  <Option value="2002">2002</Option>
                  <Option value="2003">2003</Option>
                  <Option value="2004">2004</Option>
                  <Option value="2005">2005</Option>
                  <Option value="2006">2006</Option>
                  <Option value="2007">2007</Option>
                  <Option value="2008">2008</Option>
                  <Option value="2009">2009</Option>
                  <Option value="2010">2010</Option>
                  <Option value="2011">2011</Option>
                  <Option value="2012">2012</Option>
                  <Option value="2013">2013</Option>
                  <Option value="2014">2014</Option>
                  <Option value="2015">2015</Option>
                  <Option value="2016">2016</Option>
                  <Option value="2017">2017</Option>
                </Select>
              )}
            </FormItem>
          </Col>
          <Col {...colLayout}>
            <FormItem
              {...formItemLayout}
              label={content.vt}
            >
              {getFieldDecorator('Value of trailer', {
                rules: [{
                  pattern: '^[0-9]*$' , message: content.type_number,
                }, {
                  required: true, message: content.required,
                }],
              })(
                <Input addonAfter={currency}/>
              )}
            </FormItem>
          </Col>

          <Col {...colLayout}>
            <FormItem
              {...formItemLayout}
              label={content.vv}
            >
              {getFieldDecorator('Value of vessel', {
                rules: [{
                  pattern: '^[0-9]*$' , message: content.type_number,
                },{
                  required: true, message: content.required,
                }],
              })(
                <Input addonAfter={currency}/>
              )}
            </FormItem>
          </Col>
          <Col {...colLayout}>
            <FormItem
              {...formItemLayout}
              label={content.is}
            >
              {getFieldDecorator('Insurance sum', {
                rules: [{
                  pattern: '^[0-9]*$' , message: content.type_number,
                },{
                  required: true, message: content.required,
                }],
              })(
                <Input addonAfter={currency}/>
              )}
            </FormItem>
          </Col>

          <Col {...colLayout}>
            <FormItem
              {...formItemLayout}
              label={content.tv}
            >
              {getFieldDecorator('Type of vessel', {
                rules: [{
                  required: true, message: content.required,
                }],
              })(
                <Select >
                  <Option value={content.mb}>{content.mb}</Option>
                  <Option value={content.sb}>{content.sb}</Option>
                </Select>
              )}
            </FormItem>
          </Col>

          <Col {...colLayout}>
            <FormItem
              {...formItemLayout}
              label={content.tep}
            >
              {getFieldDecorator('Total engine power', {
                rules: [{
                  pattern: '^[0-9]*$' , message: content.type_number,
                },{
                  required: true, message: content.required,
                }],
              })(
                <Input addonAfter={powerUnity}/>
              )}
            </FormItem>
          </Col>
          <Col {...colLayout}>
            <FormItem
              {...formItemLayout}
              label={content.vom}
            >
              {getFieldDecorator('Value of outboard motor', {
                rules: [{
                  pattern: '^[0-9]*$' , message: content.type_number,
                },{
                  required: true, message: content.vom,
                }],
              })(
                <Input addonAfter={currency}/>
              )}
            </FormItem>
          </Col>

          <Col {...colLayout}>
            <FormItem
              {...formItemLayout}
              label={content.ca}
            >
              {getFieldDecorator('Cruising Area', {
                rules: [{
                  required: true, message: content.required,
                }],
              })(
                <Select >
                  <Option value={content.ec}>{content.ec}</Option>
                  <Option value={content.ei}>{content.ei}</Option>
                </Select>
              )}
            </FormItem>
          </Col>
          </Row>

          <Row>
            <Col offset={2}>
              <h1>{content.pd}</h1>
            </Col>
          </Row>


          <Row gutter={8}>
          <Col {...colLayout}>
            <FormItem
              {...formItemLayout}
              label={content.sal}
            >
              {getFieldDecorator('Salutation', {
                rules: [{
                  required: true, message: content.required,
                }],
              })(
                <RadioGroup>
                  <Radio value={content.mr}>{content.mr}</Radio>
                  <Radio value={content.mrs}>{content.mrs}</Radio>
                </RadioGroup>
              )}
            </FormItem>
          </Col>
        </Row>

        <Row gutter={8}>
          <Col {...colLayout}>
            <FormItem
              {...formItemLayout}
              label={content.ln}
            >
              {getFieldDecorator('Last name', {
                rules: [{
                  type: 'string', message: '',
                }, {
                  required: true, message: content.required,
                }],
              })(
                <Input />
              )}
            </FormItem>
          </Col>

          <Col {...colLayout}>
            <FormItem
              {...formItemLayout}
              label={content.fn}
            >
              {getFieldDecorator('First name', {
                rules: [{
                  type: 'string', message: '',
                }, {
                  required: true, message: content.required,
                }],
              })(
                <Input />
              )}
            </FormItem>
          </Col>

          <Col {...colLayout}>
            <FormItem
              {...formItemLayout}
              label={content.db}
            >
              {getFieldDecorator('Date of birth', {
                rules: [{
                  required: true, message: content.required,
                }],
              })(
                <DatePicker />
              )}
            </FormItem>
          </Col>

          <Col {...colLayout}>
            <FormItem
              {...formItemLayout}
              label={content.tel}
            >
              {getFieldDecorator('Phone number', {
                rules: [{
                  pattern: '^[0-9]*$' , message: content.type_number,
                }, {
                  required: true, message: content.required,
                }],
              })(
                <Input />
              )}
            </FormItem>
          </Col>

          <Col {...colLayout}>
            <FormItem
              {...formItemLayout}
              label={content.zc}
            >
              {getFieldDecorator('Zip code', {
                rules: [{
                  pattern: '^[0-9]*$' , message: content.type_number,
                }, {
                  required: true, message: content.required,
                }],
              })(
                <Input />
              )}
            </FormItem>
          </Col>

          <Col {...colLayout}>
            <FormItem
              {...formItemLayout}
              label={content.str}
            >
              {getFieldDecorator('Street', {
                rules: [{
                  type: 'string' , message: '',
                }, {
                  required: true, message: content.required,
                }],
              })(
                <Input />
              )}
            </FormItem>
          </Col>

          <Col {...colLayout}>
            <FormItem
              {...formItemLayout}
              label={content.cty}
            >
              {getFieldDecorator('City', {
                rules: [{
                  type: 'string' , message: '',
                }, {
                  required: true, message: content.required,
                }],
              })(
                <Input />
              )}
            </FormItem>
          </Col>

          <Col {...colLayout}>
            <FormItem
              {...formItemLayout}
              label={content.mail}
            >
              {getFieldDecorator('Email', {
                rules: [{
                  type: 'email' , message: '',
                }, {
                  required: true, message: content.required,
                }],
              })(
                <Input />
              )}
            </FormItem>
          </Col>

        </Row>

        <Row >
        <Col xs={{ offset: 0 }} sm={{ offset: 5 }} >
          <Button type="primary" htmlType="submit" className="Submitbtn">
            Submit
          </Button>
        </Col>
      </Row>

      </Form>
    );
  }
}

const App = Form.create()(PantForm);

export default App;



